const HeaderStyle = {
    display : 'flex',
    justifyContent: 'center',
    // alignItems: 'center',
    backgroundColor : '#888',
    height : '100px'
}

const LogoStyle = {
  position : 'left'
}

function Header(){
  return(
    <header className="header" style={HeaderStyle}>
      <div className="logo" style={LogoStyle}>Todo Application</div>
    </header>
  )
}

export default Header
